html {
  position: relative;
  min-height: 100%;
}

body {
  margin-bottom: 60px;
  font-family: 'Segoe UI', 'Segoe UI Light', Helvetica, 'sans-serif', Arial;
}

body > .container {
  padding-top: 100px;
  background-color: white;
}

.container .text-muted {
  margin: 20px 0;
}

code {
  font-size: 80%;
}

/** NAVIGATION */
.navbar-default {
  background-color: #FFFFFF;
  border: none;
}

.navbar-default .navbar-toggle {
  border-color: transparent;
}

.navbar-default .navbar-toggle:focus, .navbar-default .navbar-toggle:hover {
  background-color: transparent;
}

.navbar-default .right-navbar-menu > li > a {
  color: #FFFFFF;
  font-size: 14px;
  /*line-height: 60px;*/
  width: 115px;
  height: 90px;
  text-align: center;
  /*padding-top: 25px;*/
  display: table-cell;
  vertical-align: middle;
}

.navbar-default .navbar-nav > li > a:hover {
  color: #063153;
  background-color: #e7e7e7;
  transition: 0.4s;
}

.navbar-default .navbar-nav > .active > a, .navbar-default .navbar-nav > .active > a:focus, .navbar-default .navbar-nav > .active > a:hover {
  color: #063153;
  font-weight: 500;
}

.navbar-default .left-navbar-menu > li > a {
  color: #FFFFFF;
}

.affix {
  top: 0;
  width: 100%;
}

.affix + .container-fluid {
  padding-top: 70px;
}

.navbar {
  margin-bottom: 0;
  border-radius: 0;
}

.navbar > .container .navbar-brand-engageiq {
  margin-left: 0;
}

.navbar-toggle-left {
  float: left;
}

.navbar-brand-engageiq {
  padding: 0;
  margin-right: 40px;
}

.navbar-brand-engageiq > img {
  height: 100%;
}

.navbar-top > li > a {
  line-height: 60px;
}

.navbar-menu > li > a {
  line-height: 60px;
}

#top-nav {
  height: 90px;
  background-color: #FFFFFF;
}

.navbar-second {
  top: 90px;
  background-color: #063153;
}

.right-navbar-menu {
  float: right;
}

.left-navbar-menu {
  float: left;
}

/** END-NAVIGATION */
/** PAGER */
.pager {
  margin: 0;
  float: right;
}

.pager li > a, .pager li > span {
  border: none;
  padding: 25px 0;
  border-radius: 0;
  width: 104px;
  font-weight: 500;
  color: white;
}

.pager li > a:hover {
  transition: 0.6s;
  opacity: 0.6;
}

/** END-PAGER */
/** LOGIN*/
.form-signin {
  max-width: 600px;
  margin: 20px auto 0;
  background-image: url("../images/login-bg.png");
  background-repeat: no-repeat;
  height: 580px;
}

.form-login {
  max-width: 360px;
  padding-top: 210px;
  margin-left: 150px;
}

.login-control {
  margin: 2px 0;
  background-color: #d9d9d9;
  border-radius: 0;
}

.btn-login {
  width: 200px;
  margin: 15px auto 0;
  border-radius: 20px;
  background-color: #ed1b24;
  border: none;
}

.btn-login:hover {
  background-color: #ed1b24;
  transition: 0.6s;
  opacity: 0.8;
}

.forgot-details {
  margin-left: 30px;
}

.text-bold {
  font-weight: bold;
}

/** END-LOGIN */
/** FOOTER */
.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  /* Set the fixed height of the footer here */
  height: 60px;
  background-color: #f5f5f5;
}

/** END-FOOTER */
/** PUBLISHER*/
.publisher-dashboard {
  background-image: url("../../../../images/advertisers/dashboard.jpg");
  height: 418px;
  margin-bottom: 0;
}

.publisher-dashboard .text-dashboard {
  font-size: 18px;
  font-weight: 400;
}

.right-dashboard {
  float: right;
  padding-top: 40px;
}

.btn:hover {
  transition: 0.5s;
  opacity: 0.8;
}

.btn-coreg {
  background-color: #063153;
  color: #FFFFFF;
}

/** STATISTICS */
.btn-link-out {
  background-color: #1c6ca9;
  color: #FFFFFF;
}

.form-control {
  border-radius: 0;
}

.btn-submit {
  background-color: #063153;
  border-color: #063153;
  color: #FFFFFF;
}

.column-header {
  background-color: #063153;
  color: #FFFFFF;
  text-align: center;
}

.column-header .revenue {
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  color: #FFFFFF;
}

.column-header .deals {
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  color: #FFFFFF;
}

.table > tbody > tr > td {
  border: 1px solid #FFFFFF;
  vertical-align: middle;
}

.table > thead > tr > th {
  vertical-align: middle;
}

.table .amount {
  text-align: right;
}

.table .sites {
  background-color: #cfeaff;
  font-weight: bold;
  color: #063153;
}

.table .total-leads {
  background-color: #edf7ff;
  color: #063153;
}

.table .total-revenue {
  background-color: #b3f1fc;
  color: #063153;
}

.table .stats-col-campaign {
  background-color: #aaebff;
  font-weight: bold;
  color: #063153;
}

.table .stats-col-leads {
  background-color: #f4edff;
  color: #063153;
}

.table .stats-col-total {
  background-color: #dfe2e3;
  color: #063153;
}

.pagination-revenue {
  margin: 0 20px 20px;
}

.row-button {
  cursor: pointer;
}

.sub-revenue {
  background-color: #3b9de8;
}

/** END-STATISTICS */
/** CAMPAIGNS */
.col-btn {
  text-align: center;
  text-transform: uppercase;
}

.col-btn > a {
  font-weight: 500;
}

.btn-get-code {
  background-color: #063153;
  color: #FFFFFF;
}

.posting-instructions-div .code-sample-div {
  height: 450px;
  overflow: scroll;
}

.campaign-col-name {
  border: 2px solid silver;
  margin: 0 10px;
  text-align: center;
  background-color: #FFFFFF;
  height: 306px;
  padding-top: 20px;
}

.campaign-title {
  color: #063153;
}

.campaign-desc {
  color: #979797;
  font-size: 17px;
  font-weight: 800;
}

.campaign-image {
  padding: 20px;
  border: 2px solid silver;
  margin: 20px 10px 0;
}

.campaign-image img {
  width: 100%;
}

.campaign-modal-content {
  background-color: #edf7ff;
}

.campaign-description {
  background-color: #FFFFFF;
  padding: 10px;
  border: 2px solid silver;
  overflow: scroll;
  /*height: 114px;*/
  height: 267px;
}

#campaigns-table > thead, #campaigns-table > tfoot, #campaigns-table > thead select, #campaigns-table > tfoot select {
  background-color: #063153;
  color: #FFFFFF;
}

/** END-CAMPAIGNS */
/** ACCOUNT INFO */
.account-info {
  max-width: 700px;
  margin: 0 auto;
  padding-top: 70px;
}

.profile-pic {
  text-align: center;
  vertical-align: middle;
}

.icon-wrapper {
  float: left;
  padding: 10px;
  min-width: 100px;
  text-align: center;
  vertical-align: middle;
}

.details-account h3 {
  color: #063153;
}

.edit-account-info {
  max-width: 600px;
  margin: 0 auto;
  padding-top: 20px;
}

.offset-update-button {
  margin-left: 24.9%;
}

/** END-ACCOUNT-INFO */
/** END-PUBLISHER */
/** TABLET AND MOBILE */
@media (min-width: 992px) {
  .right-navbar-menu-tablet {
    display: none;
  }
}

@media (max-width: 991px) {
  .right-navbar-menu {
    float: left;
  }
  .right-navbar-menu-tablet {
    display: block;
    float: right;
  }
  .navbar-default .right-navbar-menu > li > a {
    line-height: normal;
    text-align: left;
    padding-top: 10px;
    /*height: auto;*/
  }
  body > .container {
    padding-top: 150px;
  }
  .navbar-default .right-navbar-menu-tablet > li > a {
    line-height: 60px;
  }
  .right-navbar-menu-mobile {
    display: none;
  }
}

@media (min-width: 768px) {
  .offset-update-button {
    margin-left: 24.9%;
  }
  img.logo {
    width: 100%;
  }
  .dropdown-account {
    display: none;
  }
}

@media (max-width: 767px) {
  body > .container {
    padding-top: 70px;
  }
  .offset-update-button {
    margin-left: 0;
  }
  a.engageiq-logo {
    position: absolute;
    left: 35%;
    display: block;
    text-align: center;
    padding: 5px;
  }
  .engageiq-logo > img {
    height: 40px;
  }
  .right-navbar-menu {
    position: absolute;
    width: 100%;
    margin-top: 0;
  }
  .navbar-default .right-navbar-menu > li > a {
    width: 100%;
    height: auto;
    display: block;
  }
  .right-navbar-menu-tablet {
    display: none;
  }
  .right-navbar-menu-mobile {
    display: block;
  }
  .dropdown-account {
    position: relative;
    float: right;
    margin-top: 8px;
    margin-right: 15px;
    margin-bottom: 8px;
    display: inline-block;
  }
}

/** END-TABLET-AND--MOBILE */
